import React,{Component}from"react";export default class Slug extends Component{constructor(){super();this.c="lblb-slug"}render(){const{props,c}=this;const classes=[c];if(props.small)classes.push(`${c}_small`);if(props.big)classes.push(`${c}_big`);if(props.huge)classes.push(`${c}_huge`);if(props.noBg)classes.push(`${c}_no-bg`);return React.createElement("span",{className:classes.join(" ")},props.children)}}